@import 'variables.scss';
@import 'icons.scss';

@font-face {
	font-family: 'icons';
	font-weight: normal;
	font-style: normal;
	src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}

$height: 48px;

.video-js  {
	.vjs-progress-control .vjs-mouse-display {
		background-color: white;
	}
	.vjs-control-bar {
		height: $height;
	}

	.vjs-control {
		width: 60px;
		font-size: 14px;
		text-shadow: none !important;
	}

	.vjs-time-tooltip, .vjs-time-control {
		font-family: $font-primary;
		font-weight: 400;
	}

	.vjs-time-control {
		font-size: 16px;
		line-height: $height;
		padding: 0 ;
	}

	.vjs-volume-bar {
		margin: 21px 0.48em;
	}

	// ELEMENT ORDERING
	.vjs-fullscreen-control {
		order: 2;
	}
	.vjs-play-control {
		order: 0;
	}
	.vjs-volume-panel {
		order: 1;
	}

	button {
		outline: none;

		&.vjs-big-play-button {
			bottom: 0;
			left: 0;
			position: absolute;
			margin-right: auto;
			transition: all 0.6s $easing3;
			margin-left: 33px;
			margin-bottom: 33px;
			margin-top: auto;
			border: solid 2px;
			width: 75px;
			height: 75px;
		}
		&:hover {
			button {
				&.vjs-big-play-button {
					background-color: rgba($brand-black, 0.6);
				}
			}
		}
		.vjs-has-started:not(:hover) .vjs-dock-text, .vjs-has-started:not(:hover) .vjs-dock-shelf {
			transition: all 0.9s $easing3 !important;
		}
	}


	// ICONOGRAPHY
	.vjs-button .vjs-icon-play, 
	.vjs-big-play-button .vjs-icon-placeholder, 
	.vjs-icon-pause, .video-js .vjs-play-control.vjs-playing .vjs-icon-placeholder {
		@include iconcss();
	}

	.vjs-icon-play, 
	.video-js .vjs-big-play-button .vjs-icon-placeholder:before, 
	.video-js .vjs-play-control .vjs-icon-placeholder {
		font-family: inherit;
	}

	.vjs-icon-play:before, 
	.vjs-big-play-button .vjs-icon-placeholder:before, 
	.vjs-play-control .vjs-icon-placeholder:before {
		content: "\EA18";
	}

	.vjs-icon-pause:before, 
	.vjs-play-control.vjs-playing .vjs-icon-placeholder:before {
		content: "\EA62";
	}


	// VIDEO TITLE

	.vjs-dock-text {
		background: linear-gradient(rgba($brand-black, 0.8), rgba($brand-black, 0));
		padding: 33px;

		.vjs-dock-title, .vjs-dock-description {
			font-family: $font-primary;
		}

		.vjs-dock-title {
			font-weight: 800;
			font-size: 24px;
			line-height: 1.2;
		}

		.vjs-dock-description {
			font-weight: 400;
			font-size: 16px;
			line-height: 20px;
		}
	}


	// PROGRESS BAR

	.vjs-progress-control {
		width: 100%;
		transition: all 0.3s $easing2 !important;
	}

	.vjs-load-progress {
	    background: rgba($brand-black, 0.4);
	}


	&.vjs-has-started.vjs-paused.vjs-user-active:not(:hover) .vjs-control-bar,
	&.vjs-has-started.vjs-playing.vjs-user-active:not(:hover) .vjs-control-bar,
	&.vjs-has-started.vjs-paused.vjs-user-inactive:not(:hover) .vjs-control-bar,
	&.vjs-has-started.vjs-playing.vjs-user-inactive:not(:hover) .vjs-control-bar,
	&.vjs-has-started.vjs-playing.vjs-user-inactive .vjs-control-bar,
	&.vjs-has-started.vjs-paused.vjs-ad-playing.vjs-user-inactive .vjs-control-bar {
		transition: all 0.9s $easing3 !important;
		transform: translateY($height) !important;
	}

	& .vjs-volume-panel:hover .vjs-volume-control.vjs-volume-horizontal, 
	& .vjs-volume-panel:active .vjs-volume-control.vjs-volume-horizontal, 
	& .vjs-volume-panel:focus .vjs-volume-control.vjs-volume-horizontal, 
	& .vjs-volume-panel .vjs-volume-control:hover.vjs-volume-horizontal, 
	& .vjs-volume-panel .vjs-volume-control:active.vjs-volume-horizontal, 
	& .vjs-volume-panel .vjs-mute-control:hover ~ .vjs-volume-control.vjs-volume-horizontal, 
	& .vjs-volume-panel .vjs-volume-control.vjs-slider-active.vjs-volume-horizontal {
		height: $height !important;
	}

	.vjs-button > .vjs-icon-placeholder:before {
		position: relative;
	}
}

